export const isLastSubTask = (subTaskId, subTasksList) =>
  !subTasksList.length ||
  (subTasksList.length === 1 && subTasksList.some(({ id }) => id === subTaskId));

export const groupSubTasksByTask = subTasksList =>
  subTasksList.reduce((groupedSubTasks, subTask) => {
    // eslint-disable-next-line no-param-reassign
    groupedSubTasks[subTask.taskId] = groupedSubTasks[subTask.taskId] || [];
    groupedSubTasks[subTask.taskId].push(subTask);

    return groupedSubTasks;
  }, {});
