import { createReducer } from '@reduxjs/toolkit';
import { removeById } from 'helpers/genericHelpers';
import { groupSubTasksByTask } from 'helpers/subTaskHelpers';
import { subTasksSetAction, setSubTasksOfTaskAction, subTaskDeleteSucceedAction } from './actions';

const initialTasksState = {
  allSubTasks: {},
};

const subTasksReducer = createReducer(initialTasksState, builder => {
  builder
    .addCase(setSubTasksOfTaskAction, (state, { payload: { taskId, subTasks } }) => ({
      ...state,
      allSubTasks: {
        ...state.allSubTasks,
        [taskId]: subTasks,
      },
    }))
    .addCase(subTasksSetAction, (state, { payload: subTasks }) => ({
      ...state,
      allSubTasks: groupSubTasksByTask(subTasks),
    }))
    .addCase(
      subTaskDeleteSucceedAction,
      (state, { payload: { id: removedSubTaskId, taskId } }) => ({
        ...state,
        allSubTasks: {
          ...state.allSubTasks,
          [taskId]: removeById(state.allSubTasks[taskId], removedSubTaskId),
        },
      }),
    );
});

export default subTasksReducer;
